import React, {createRef} from 'react';
import * as FIcon from 'react-feather';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import './AdminTransportista.css';
import {Page, SDataTable} from "../../components";
import {RouteComponentProps} from "react-router";
import {openPageNow} from "../../helpers";
import {RUTAS} from "../../constants";
import { useToaster } from '../../helpers-ts'

const moment = require('moment-timezone')

export const AdminTransportista: React.FC<RouteComponentProps> = observer(({history}) => {
  const rootStore = useStores()
  const DataTable = createRef<any>()
  const Estado = history.location.pathname.split('/')[3]
  const addToast = useToaster()

  React.useEffect(() => {
    DataTable.current.refresh()
  }, [window.location.pathname])

  const visualizar = (row: any) => {
    openPageNow(RUTAS.ADMIN_TRANSPORTISTA_DETAIL(Estado, row.id), history)
  }

  const editar = (row: any) => {
    openPageNow(RUTAS.ADMIN_TRANSPORTISTA_UPDATE(Estado, row.id), history)
  }

  const eliminar = (row: any) => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea ejecutar la acción?')) {
      return false
    }
    const accion = row.aprobado ? row.bloqueado ? 'Desbloqueado' : 'Bloqueado' : 'Eliminado'
    const dtable = DataTable.current
    rootStore.environment.api.eliminarTransportista(row.id).then((result: any) => {
      if (result.kind === "ok") {
        addToast(accion + ' satisfactoriamente', 'success')
        dtable.refresh()
      } else {
        addToast('Ocurrió un error al ejecutar la acción, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const renderVencimientoRegistro = (row: any) => {
    if (moment(row.registro_vencimiento).diff(moment(), 'days') < 1) {
      return <span style={{color: 'red'}}
                   title={'Registro vencido'}>{moment(row.registro_vencimiento).format('DD/MM/YYYY')}</span>
    } else if (moment(row.registro_vencimiento).diff(moment(), 'days') < 30) {
      return <span style={{color: 'orange'}}
                   title={'Registro por vencer'}>{moment(row.registro_vencimiento).format('DD/MM/YYYY')}</span>
    }
    return moment(row.registro_vencimiento).format('DD/MM/YYYY')
  }

  const renderAcciones = (row: any) => {
    const iconoEliminar = row.aprobado ? row.bloqueado ? <FIcon.Check size={14}/> : <FIcon.Slash size={14}/> : <FIcon.Trash size={14}/>
    return <div>
      <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}><FIcon.Eye size={14}/></span>
      <span className={'button ml-3 '} title={'Editar'} onClick={() => editar(row)}><FIcon.Edit size={14}/></span>
      <span className={'button ml-3'} title={row.aprobado ? row.bloqueado ? 'Desbloquear' : 'Bloquear' : 'Eliminar'} onClick={() => eliminar(row)}>{iconoEliminar}</span>
    </div>
  }

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{display: "flex", height: "100vh", overflowY: "scroll"}}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Drivers</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item"><a onClick={() => history.push('/admin/')}>Inicio</a></li>
                <li className="breadcrumb-item active">Drivers</li>
              </ol>
            </div>
          </div>
          {Estado === 'pendiente' && <ul className="nav nav-pills p-3 bg-white mb-3 align-items-center">
            <li className="nav-item ml-auto">
              <button className="btn btn-primary d-flex align-items-center px-3" id="add-tipo-vehiculo"
                      onClick={() => openPageNow(RUTAS.ADMIN_TRANSPORTISTA_CREATE, history)}>
                <span className="d-none d-md-block">Crear Driver</span>
              </button>
            </li>
          </ul>}
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  if (order.endsWith('nombre')) {
                    order = order.replace('nombre', 'usuario__first_name')
                  }
                  if (order.endsWith('apellido')) {
                    order = order.replace('apellido', 'usuario__last_name')
                  }
                  let moderado = history.location.pathname.split('/')[3] !== 'pendiente'
                  return rootStore.environment.api.transportistas(page, perPage, order, search, moderado)
                }}
                columns={[
                  {
                    name: 'ID',
                    selector: 'id',
                    sortable: true,
                  },
                  {
                    name: 'Imagen',
                    selector: 'imagen_perfil',
                    cell: (row: any) => row.imagen_perfil ? <img src={row.imagen_perfil} style={{
                      maxWidth: 50,
                      maxHeight: 50,
                      borderRadius: 10,
                      background: '#ededed'
                    }}/> : '',
                  },
                  {
                    name: 'Nombre',
                    selector: 'nombre',
                    sortable: true,
                  },
                  {
                    name: 'Apellido',
                    selector: 'apellido',
                    sortable: true,
                  },
                  {
                    name: 'Vencimiento del registro',
                    selector: 'registro_vencimiento',
                    sortable: true,
                    cell: renderVencimientoRegistro,
                  },
                  {
                    name: 'Aprobado',
                    selector: 'aprobado',
                    sortable: true,
                    cell: (row: any) => row.aprobado ? 'Sí' : 'No',
                  },
                  {
                    name: 'Bloqueado',
                    selector: 'bloqueado',
                    sortable: true,
                    cell: (row: any) => row.bloqueado ? 'Sí' : 'No',
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
});

export default AdminTransportista;
